.App {
  text-align: center;
}

.Card {
  position: absolute;
  left: 0;
  right: 0;
  margin: auto;
}

.Deck {
  font-family: Arial, Helvetica, sans-serif;
}

.Deck-cardarea {
  margin-top: 80px;
}

.Deck-title {
  color: azure;
  font-size: 40px;
  text-transform: uppercase;
  letter-spacing: 5px;
}

.subtitle {
  font-size: 20px;
}

.Deck-btn {
  background: none;
  border: 2px solid white;
  outline: none;
  font: inherit;
  margin: 0.5em;
  padding: 1em 2em;
  color: #009688;
  cursor: pointer;
  transition: .25s;
}

.Deck-btn:hover {
  border-color: #4caf50;
  color: wheat;
  box-shadow: inset 0 0 0 2em #4caf50;
}